import Vue from 'vue'
import Vuex from 'vuex'
import VueJwtDecode from "vue-jwt-decode"
import { deffSettings } from './config.js'

Vue.use(Vuex)

let state = {
    myself: {},
    allUsers: []
}

let mutations = {    
    
    setMyself (state, __admin) {
        state.myself =  __admin;
    },
    
    setAllUsers (state, __users) {
        state.allUsers =  __users;
    }
}

let getters = {

    session: () => {

        const _sessToken = window.localStorage.getItem('jwt')

        if (_sessToken) {
            return VueJwtDecode.decode(_sessToken)
        }

        return {}
    },

    settings: (state) => {
        
        if (state.myself.name) {
            return state.myself.settings
        }
        
        return deffSettings
    }
}

let actions = {

    fetchAndSetMyself ({ commit }, $p) {

        const _notLoginPage = $p.route.name !== 'Login',
              _sessToken = window.localStorage.getItem('jwt');

        if (_notLoginPage && _sessToken) {

            console.log('Init fetchAndSetMyself...')

            $p.axios.get('/getMyself').then(_res => {
                commit('setMyself', _res.data)
                console.log('state.myself set sucessfully')
            }).catch(_err => {
                console.log('Fail at fetchAndSetMyself()')
                console.error(_err)
            })
        }
    },

    fetchAndSetAllUsers ({ commit }, $p) {

        const _notLoginPage = $p.route.name !== 'Login',
              _sessToken = window.localStorage.getItem('jwt');

        if (_notLoginPage && _sessToken) {

            console.log('Init fetchAndSetMyself...')

            $p.axios.get('/admin/getAllUsers').then(_res => {
                commit('setAllUsers', _res.data)
                console.log('state.users set sucessfully')            
            }).catch(_err => {
                console.log('Fail at fetchAndSetAllUsers()')
                console.error(_err)
            })
        }
    }
}

export default new Vuex.Store({
    state,
    mutations,
    actions,
    getters
})
