import axios from 'axios'

function setStatusInfo (_info, _status) {
    _info.status = _status
    _updateInfo.call(this, _info)
}

function setConsultableInfo (_info) {

    // from crud or table list
    let info = _info ? _info : this.info;

    info.consultable = true
    info.status = 'Novo'

    _updateInfo.call(this, info)
}

function _updateInfo ($info) {

    const _URL = this.win.backBaseURL + '/editInfo';

    axios.post(_URL, $info).then(_r => {

        this.$notify('Info EDITADA sucesso!')

    }).catch(_err => {

        const _errTxt = 'Fail to load _updateInfo...';

        alert(_errTxt, _err)
        console.error(_errTxt, _err)
    })
}

export default {
    setStatusInfo,
    setConsultableInfo
}