function infoToClipboard (_info, _demo) {

    let _resTxt = this.infoObjToText(_info, _demo),
        _notifyTxt = '';

    _notifyTxt = _demo ? "BIN copiada!" : 'Info copiada!'

    if (_info.constructor.name === 'Array') {
        _notifyTxt = _demo ? "BIN's copiadas!" : "Infos copiadas"
    }

    this.copyTextAndNotify(
        _resTxt, 
        _notifyTxt)
}

export default infoToClipboard