        app 
        dark 
        clipped>

        <v-list
            dense
            nav>
            <v-list-item-group>

                <v-list-item 
                    v-if="!_rt.nolist && _rt.icon"
                    v-for="_rt in rawRoutes"
                    :key="_rt.name"
                    :href="'#' + _rt.path">
                  <v-list-item-icon>
                    <v-icon>mdi-{{_rt.icon}}</v-icon>
                  </v-list-item-icon>
