                    <v-list-item-title>{{_rt.name}}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>

            </v-list-item-group>
        </v-list>

        <div class="pa-5 white--text">

            <div>
                <label>user:</label>
                <span class="float-right">
                    <span>{{myself.name}}</span>
                    <v-icon dense class="ml-2">mdi-account</v-icon>
                </span>
            </div>

