                <span class="float-right">
                    <span>{{myself._role}}</span>
                    <v-icon dense class="ml-2">mdi-account-cog</v-icon>
                </span>
            </div>

            <br>

            <v-btn 
                to:="/app"
                color="primary" 
                small
                block>
                <v-icon small>mdi-arrow-left</v-icon>
                <span class="ml-1">voltar app</span>
            </v-btn>

        </div>

    </v-navigation-drawer>

    <v-main 
        v-if="myself._role !== 'usual'"
        style="background-color: whitesmoke">
        <v-container class="pa-4 pl-6 pt-6 pr-6">
            <router-view v-if="myself._role !== 'usual'" /> 
        </v-container>
    </v-main>

    <v-row 
        v-if="myself._role === 'usual'"
        class="pa-10">
        
        <v-col cols="12" md="12">
            Error: { code: 404, message: "Not Found" }
        </v-col>
    </v-row>
    
  </v-app>
</template>

<script>

    import PageHeader from './components/PageHeader.vue'

    export default {

        name: 'App',

        data () {
            return {
                drawerActive: true
            }
        },

        methods: {

            logout () {
                window.localStorage.removeItem("jwt")
                this.$router.push('login')
            },

            orientate (_route) {
                
                if (_route.meta.landscape) {
                    this.forceLandscape(true)
                } else {
                    this.forceLandscape(false)
                }

                setTimeout(() => {
                    window.scrollTo(0, 1)
                }, 1000)
            },

            fullscreen () {

                if (!document.fullscreenElement) {
                    document.documentElement.requestFullscreen()
                } else {
                    if (document.exitFullscreen) {
                        document.exitFullscreen()
                    }
                }
            },

            setAxiosDefaults () {

                const _env = process.env.NODE_ENV,
                      _sessToken = window.localStorage.getItem('jwt');

                if (_sessToken) {
                    this.axios.defaults.headers.common['Authorization'] = `Bearer ${_sessToken}` 
                }

                if (_env === 'production') {
                    this.axios.defaults.baseURL = window.location.origin + '/services/'
                }

                if (_env === 'development') {
                    this.axios.defaults.baseURL = `http://${window.location.hostname}:3500/services/`
                }
            }
        },

        created () {
            
            this.setAxiosDefaults()
            
            this.fetchAndSetMyself({
                axios: this.axios, 
                route: this.$route
            })

            this.fetchAndSetAllUsers({
                axios: this.axios, 
                route: this.$route
            })
        },

        components: {
            PageHeader
        }

    };

</script>