<style>
    
</style>

<template>
    <div id="PageHeader">
        
        <v-subheader>
            <v-icon dense>mdi-{{currPageData.icon}}</v-icon>
            <v-breadcrumbs
                divider="/"
                :items="[
                    { text: currPageData.text, href: currPageData.href, disabled: true }
                ]" />
        </v-subheader>
            
    </div>
</template>

<script>

    export default {
        
        name: 'PageHeader',

        props: {
            overhide: String
        },

        computed: {

            currPageData () {

                let _s = {};
                
                _s.icon = ''
                _s.text = ''
                _s.href = ''
                
                if (this.$route.name) {

                    let _rr = this.rawRoutes.find(_rr => {
                        return _rr.name === this.$route.name
                    })
                    
                    _s.icon = _rr.icon
                    _s.text = _rr.name
                    _s.href = _rr.path
                }

                return _s
            }
        }
    }
</script>