import { 
    infoPropsMap, 
    checkBoolsMap } from '../config.js'

function infoObjToText  (_infoObj, _demo) {
    
    // * hope formart _infoObj
    // = output from infoText + database props

    let main = (__infoObj) => {

        let _infoValues = Object.values(__infoObj);
        let _toText = '';

        if (__infoObj._errors) {
            
            let _errText = ` ${__infoObj._errors.join(', ')} ${checkBoolsMap._valid[1]}|`,
                _ios = __infoObj

            _toText = `${_ios.number}|${_ios.month}|${_ios.year}|${_ios.cvv}|${_ios.level}|${_ios.type}|${_ios.card}`
            _toText = _toText + `|${_errText}`
        }

        if (!__infoObj._errors) {

            _infoValues.forEach((__val, _i) => {

                let _prop = infoPropsMap[_i],
                    _val;

                if (__infoObj[_prop]) {

                    if (checkBoolsMap[_prop]) {

                        let _cbm = checkBoolsMap[_prop]

                        _val = __infoObj[_prop] ? _cbm[0] : _cbm[1] 
                    
                    } else {
                        _val = __infoObj[_prop]
                    }

                    _toText = _toText + `${_val}|`
                
                }
            })
        }

        // remove last |
        _toText = _toText.slice(0, -1)

        // ready return
        return _toText
    }

    if (_demo) {
        main = demoTextMode;
    }

    if (_infoObj.constructor.name === 'Array') {
        
        let _txtArray = _infoObj.map(_obj => {
            return main(_obj)
        })

        return _txtArray.join('\n\n') // txt raw 
    }

    if (_infoObj.constructor.name === 'Object') {
        return main(_infoObj)
    }
}

function demoTextMode (_io) {

    let _text = `
        #${_io.number.substring(0, 8)}|
        ${_io.level}|
        ${_io.card}|
        ${_io.type}|
        ${_io._valid ? checkBoolsMap._valid[0] : checkBoolsMap._valid[1]}
        ${_io._live ? checkBoolsMap._live[0] : checkBoolsMap._live[1]}
    `

    _text = _text.replaceAll(' ', '')
    _text = _text.replaceAll('\n', '')

    return _text
}

export default infoObjToText