<style>

    #Dashboard td:nth-child(1) { width: 10em !important }
    #Dashboard td:nth-child(2) { max-width: 5em !important }
    #Dashboard td:nth-child(3) { max-width: 7vw !important }
    #Dashboard td:nth-child(4) { max-width: 1vw !important }
    #Dashboard td:nth-child(5) { max-width: 2vw !important }
    #Dashboard td:nth-child(6) { max-width: 1vw !important }
    
</style>

<template>
    <div id="Dashboard">

        <v-card>

            <collection-filter 
                v-model="usersFiltered"
                :items="allUsers"
                :filterRows="[

                    [

                        {   
                            label: 'opacc',
                            md: 'auto',
                            btn: true,
                            text: 'abrir conta',
                            icon: 'creation',
                            onClick: () => {
                                dialogCreateAccount.show = true
                            }
                        },

                        {
                            label:'plan',
                            md: 'auto',
                            btn: true,
                            text: 'planos',
                            color: 'primary',
                            icon: 'content-copy',
                            onClick: () => { copyPlansText() }
                        }
                    ]

                ]">

                <div slot="searchInput">
                    <v-text-field
                        class="pt-0 mt-0"
                        v-model="search"
                        prepend-icon="mdi-magnify"
                        label="Procurar"
                        single-line
                        hide-details />
                </div>

            </collection-filter>
            
            <v-data-table 
                class="list-data-table"
                mobile-breakpoint="0"
                calculate-widths
                :fixed-header="true"
                :loading="loading"
                :disabled="loading"
                :items="allUsers"
                :search="search"
                :items-per-page="-1"
                :headers="[
                    { text: 'rate', value: '_hash' },
                    { text: 'nick', value: 'name' },
                    { text: 'role', value: '_role' },
                    { text: 'creds.', value: 'credits' },
                    { text: 'checks', value: 'checksCount' },
                    { text: 'ativo', value: 'active' },
                    { text: 'wp.', value: 'whatsapp' },
                    { text: '', value: 'actions', sortable: false } 
                ]">

                <template v-slot:item._hash="{item}">
                    <div>
                        <v-icon small class="mr-2">mdi-star</v-icon>
                        <small>{{ parseInt(item.checksCount / 70)}} </small>
                    </div>
                </template>

                <template v-slot:item.name="{item}">
                    <div style="cursor: pointer" 
                         @click="dialogEditAccount.user = item; dialogEditAccount.show = true">
                        <v-icon small class="mr-2">mdi-account</v-icon>
                        <small>{{item.name}}</small>
                    </div>
                </template>

                <template v-slot:item.whatsapp="{item}">
                    <div>
                        <v-icon 
                            small 
                            color="green"
                            class="mr-2">mdi-whatsapp</v-icon>
                        <a 
                            :href="`https://api.whatsapp.com/send?phone=${item.whatsapp}`" 
                            target="_blank">
                            {{item.whatsapp}}
                        </a>
                    </div>
                </template>

                <template v-slot:item.active="{item}">
                    <div>
                        <v-icon 
                            small 
                            color="green"
                            v-if="item.active"
                            class="mr-2">mdi-check-circle</v-icon>
                        <v-icon 
                            small 
                            color="red"
                            v-if="!item.active"
                            class="mr-2">mdi-close-circle</v-icon>
                    </div>
                </template>

            </v-data-table>

        </v-card>

        <v-dialog 
            v-model="dialogCreateAccount.show"
            @click:outside="dialogCreateAccount.show = false; dialogCreateAccount.user = {}"
            transition="dialog-bottom-transition" 
            max-width="600">
            <v-card class="pb-0">
                <v-toolbar color="dark" dark>
                    <v-icon class="mr-3">mdi-creation</v-icon>
                    Abrir conta
                </v-toolbar>
                <v-card-text>
                    <div class="text-h2 pa-9 pb-0">
                        
                        <v-form v-model="isFormValid" @submit.prevent="submitNewAccount">
                            <v-row>
                                <v-col md="6">
                                    <v-text-field
                                        autofocus
                                        class="pt-0 mt-0"
                                        v-model="dialogCreateAccount.user.name"
                                        prepend-icon="mdi-card-account-details"
                                        :rules="[ruls.required]"
                                        label="nick"/>
                                </v-col>
                                <v-col md="6">
                                    <v-text-field
                                        class="pt-0 mt-0"
                                        v-model="dialogCreateAccount.user.password"
                                        :rules="[ruls.required]"
                                        prepend-icon="mdi-key-variant"
                                        append-icon="mdi-autorenew"
                                        @click:append="genPassword"
                                        label="Senha"/>
                                </v-col>
                                <v-col md="12">
                                    <v-text-field
                                        single-line
                                        class="pt-0 mt-0"
                                        prepend-icon="mdi-whatsapp"
                                        :rules="[ruls.required]"
                                        v-model="dialogCreateAccount.user.whatsapp"
                                        label="Whats" />
                                </v-col>
                                <v-col md="10" class="mb-0">
                                    <v-select
                                        class="pt-0 mt-0"
                                        prepend-icon="mdi-calendar-clock"
                                        v-model="dialogCreateAccount.user.currentPlan"
                                        :items="paymentPlans"
                                        :rules="[ruls.required]"
                                        item-text="name"
                                        item-value="value"
                                        label="Plano" />
                                </v-col>
                                <v-col class="pa-0 mb-0">
                                    <v-btn 
                                        block
                                        color="black"
                                        class="white--text pa-3 ml-3"
                                        type="submit">
                                        <v-icon small>mdi-check-circle-outline</v-icon>
                                    </v-btn>
                                </v-col>
                            </v-row>
                        </v-form>

                    </div>
                </v-card-text>
            </v-card>
        </v-dialog>


        <v-dialog 
            v-model="dialogEditAccount.show"
            @click:outside="dialogEditAccount.show = false"
            transition="dialog-bottom-transition" 
            max-width="300">
            <v-card>
                <v-toolbar color="dark" dark>
                    Menu
                    <v-icon class="ml-2">mdi-circle-edit-outline</v-icon>
                </v-toolbar>
                <v-card-text>
                    <div class="text-h2 pa-3">

                        <v-row>
                            <v-col cols="12" md="12" class="mt-3">
                                <v-btn 
                                    small
                                    dark
                                    block
                                    @click="dialogEditAccount.show = false; dialogRenewAccountPlan.show = true">
                                    <v-icon 
                                        small
                                        class="mr-2">mdi-calendar-clock</v-icon>
                                    att. plano
                                </v-btn>
                            </v-col>

                            <v-col cols="12" md="12" class="mt-3">
                                <v-btn 
                                    small
                                    dark
                                    block
                                    @click="dialogEditAccount.show = false; dialogChangeAccountPass.show = true">
                                    <v-icon 
                                        small
                                        class="mr-2">mdi-key-variant</v-icon>
                                    att. senha
                                </v-btn>
                            </v-col>

                            <v-col cols="12" md="12">
                                <v-btn 
                                    small
                                    dark
                                    block
                                    @click="dialogEditAccount.show = false; 
                                            deleteListedUser(dialogEditAccount.user._id)">
                                    <v-icon 
                                        small
                                        class="mr-2">mdi-delete-off</v-icon>
                                    Apagar
                                </v-btn>
                            </v-col>
                        </v-row>
                    </div>
                </v-card-text>
            </v-card>
        </v-dialog>

        <v-dialog 
            v-model="dialogRenewAccountPlan.show"
            @click:outside="dialogRenewAccountPlan.show = false"
            transition="dialog-bottom-transition" 
            max-width="500">
            <v-card v-if="dialogEditAccount.user.name">
                
                <v-toolbar color="dark" dark>
                    Atualizar plano
                    <v-icon class="ml-2">mdi-calendar-clock</v-icon>
                </v-toolbar>
                
                <v-card-text>
                    <div class="pa-3">
                        <v-row>
                            <v-col cols="12" md="12">
                                <small>plano atual: </small>
                                <span>{{planObjToText(dialogEditAccount.user.currentPlan)}}</span>

                                <small class="ml-6">status: </small>
                                <span>{{checkPlanActive(dialogEditAccount.user.currentPlan)}}</span>
                            </v-col>
                            <v-col cols="12" md="9" class="mt-3">
                                <v-select
                                    dense
                                    class="pt-0 mt-0"
                                    prepend-icon="mdi-calendar-clock"
                                    v-model="dialogRenewAccountPlan.newPlan"
                                    :items="paymentPlans"
                                    :rules="[ruls.required]"
                                    item-text="name"
                                    item-value="value"
                                    label="novo plano" />
                            </v-col>
                            <v-col md="3">
                                <v-btn 
                                    dense
                                    dark
                                    block
                                    style="padding: 0 !important;
                                           width: 40px !important;
                                           min-width: 40px !important"
                                    @click="renewUserPlan">
                                    <v-icon dense>mdi-check-circle</v-icon>
                                </v-btn>
                            </v-col>
                        </v-row>
                    </div>
                </v-card-text>
                
            </v-card>
        </v-dialog>

        <v-dialog 
            v-model="dialogChangeAccountPass.show"
            @click:outside="dialogChangeAccountPass.show = false"
            transition="dialog-bottom-transition" 
            max-width="500">
            <v-card v-if="dialogEditAccount.user.name">
                
                <v-toolbar color="dark" dark>
                    Atualizar plano
                    <v-icon class="ml-2">mdi-calendar-clock</v-icon>
                </v-toolbar>
                
                <v-card-text>
                    <div class="pa-3">
                        <v-row>
                            <v-col cols="12" md="9" class="mt-3">
                                <v-text-field
                                    dense
                                    class="pt-0 mt-0"
                                    prepend-icon="mdi-key-variant"
                                    v-model="dialogChangeAccountPass.newPassword"
                                    :rules="[ruls.required]"
                                    label="Nova senha" />
                            </v-col>
                            <v-col md="3">
                                <v-btn 
                                    dense
                                    dark
                                    block
                                    style="padding: 0 !important;
                                           width: 40px !important;
                                           min-width: 40px !important"
                                    @click="updatePassword">
                                    <v-icon dense>mdi-check-circle</v-icon>
                                </v-btn>
                            </v-col>
                        </v-row>
                    </div>
                </v-card-text>
                
            </v-card>
        </v-dialog>

    </div>
</template>

<script>

    import moment from 'moment'
    import userSchema from '../../schemas/user.js'
    import { paymentPlans } from '../../config.js'

    export default {
        
        name: 'Dashboard',
        
        data () {
            return {
                paymentPlans,
                ///////////////
                isFormValid: null,
                /////////////
                dialogCreateAccount: {
                    show: false,
                    user: {
                        name: '',
                        password: '',
                        whatsapp: ''
                    }
                },
                //////////////////////
                dialogEditAccount: {
                    show: false,
                    user: {}
                },
                //////////////////////
                dialogRenewAccountPlan: {
                    show: false,
                    newPlan: {}
                },
                //////////////////////
                dialogChangeAccountPass: {
                    show: false,
                    newPassword: null
                },
                //////////////////////
                waitingBinCopy: false,
                /////////////////////
                search: '',
                loading: false,
                usersFiltered: []
            }
        },

        methods: {

            submitNewAccount () {
                
                if (this.isFormValid) {

                    console.log('Adding new account...')
                    
                    this.loading = true;

                    let _ust = this.dialogCreateAccount.user
                    
                    _ust._entryDate = _ust.currentPlan.startAt = new Date();
                    _ust.credits = _ust.currentPlan.credits

                    let data = {
                        ...userSchema,
                        ..._ust
                    }

                    this.axios({
                        url: '/admin/newUserAccount',
                        method: 'post',
                        data
                    }).then(_r => {
                        this.copyTextAndNotify(
                            _ust.name + '\n' + _ust.password,
                            'Conta criada copiada  area de trabalho', () => {
                                this.loading = false;
                                this.dialogCreateAccount.show = false;
                            })
                    }).catch(_err => {
                        this.loading = false;
                        this.notify(_err.toString(), 'error')
                    })
                }
            },

            genPassword () {
                this.dialogCreateAccount
                    .user.password = Math.random()
                                        .toString(36)
                                            .slice(-8)
            },

            updateUserAny (_user, _cb) {
                this.axios({
                    url: '/admin/updateUser',
                    method: 'post',
                    data: _user
                }).then(_r => {
                    _cb(_r.data)
                }).catch(_err => {
                    this.notify(_err.toString(), 'error')
                    this.loading = false;
                })
            },

            deleteListedUser (_id) {
                
                this.loading = true;

                this.axios({
                    url: '/admin/deleteUser/' + _id,
                    method: 'post'
                }).then(_r => {
                    this.notify('User deletada!', 'info')
                    this.loading = false;
                }).catch(_err => {
                    this.notify(_err.toString(), 'error')
                    this.loading = false;
                })
            },

            renewUserPlan () {
                this.dialogEditAccount.user.currentPlan = this.dialogRenewAccountPlan.newPlan
                this.dialogEditAccount.user.currentPlan.startAt = new Date()
                this.dialogEditAccount.user.credits = this.dialogRenewAccountPlan.newPlan.credits
                this.updateUserAny(this.dialogEditAccount.user, () => {
                    this.notify('Plano atualizado!', 'success')
                })
            },

            async updatePassword () {
                
                console.log('Init updatePassword...')
                
                let data = {
                    userID: this.dialogEditAccount.user._id,
                    newPassword: this.dialogChangeAccountPass.newPassword,
                }

                if (data.newPassword.length < 3) {
                    this.notify('Senha deve conter pelo menos 3 dgitos.', 'error')
                    return
                }

                this.axios({
                    url: '/admin/updatePassword',
                    method: 'post',
                    data
                }).then(_res => {
                    this.notify('Senha alterada!', 'success')          
                }).catch(_err => {
                    this.notify(_err, 'error')
                    console.error('Fail at updatePassword()', _err)
                })
            },

            copyPlansText () {
                
                let plansTxtArr = paymentPlans.map(pp => {
                    return pp.name
                })

                let plansTxtRaw = plansTxtArr.join('\n')

                this.copyTextAndNotify(plansTxtRaw, 'Planos copiados!')
            },

            planObjToText (p) {
                return `${p.icon} ${p.days} | ${p.credits} chk | $${p.price}`
            },

            checkPlanActive (p) {
                
                let now = moment(),
                    entry = moment(p.entryDate),
                    expire = moment(entry).add('days', p.days);

                return now.isBefore(expire) ? 'Ativo' : 'Expirado'
            }
        },

        created () {
            if (this.myself._role === 'usual') {
                window.open('/app', '_self')
            }
        }
    }

</script>