import Vue from 'vue'
import VueRouter from 'vue-router'

import Dashboard from './components/pages/Dashboard.vue'

Vue.use(VueRouter)

const rawRoutes = [

  {
    path: '/',
    name: 'Dashboard',
    component: Dashboard,
    icon: 'credit-card-check',
    nolist: true
  }
]

const router = new VueRouter({
    routes: rawRoutes
})

router.beforeEach((to, from, next) => {

    const _sessionToken = window.localStorage.getItem("jwt")

    if (!_sessionToken && to.name !== 'Login') {
        next('/login')
    } else {
        next()
    }
})

export {
    rawRoutes
}

export default router