<style>
    
    .v-speed-dial {
        position: fixed;
        bottom: 15px;
        right: 15px;
    }

</style>

<template>
    <div id="FloatActionsButton">


        
        <v-speed-dial
            v-if="!single"
            v-model="active"
            bottom
            right
            direction="left"
            transition="slide-x-reverse-transition">
            
            <template v-slot:activator>
                <v-btn
                    v-model="active"
                    :color="`${color} darken-2`"
                    dark
                    fab>
                    <v-icon v-if="active">
                        mdi-close
                    </v-icon>
                    <v-icon v-else>
                        mdi-{{icon}}
                    </v-icon>
                </v-btn>
            </template>

            <v-btn
                v-for="(btn, i) in buttons"
                :key="`#${i}`"
                fab
                dark
                small
                @click="btn.onClick"
                :color="btn.color">
                <v-icon :small="btn.small">
                    mdi-{{btn.icon}}
                </v-icon>
            </v-btn>

        </v-speed-dial>

        <div 
            v-if="single" 
            class="v-speed-dial">
            <v-btn
                :color="`${color} darken-2`"
                dark
                fab>
                <v-icon>
                    mdi-{{icon}}
                </v-icon>
            </v-btn>
        </div>

    </div>
</template>

<script>
        
    export default {

        name: 'FloatActionsButton',

        data () {
            return {
                active: true
            }
        },

        props: {

            single: {
                type: Boolean,
                default: false
            },

            color: {
                type: String,
                default: 'primary'
            },

            icon: {
                type: String,
                default: 'menu'
            },
            
            autoActive: {
                type: Boolean,
                default: false
            },

            buttons: Array 

            /* -> buttons schema
                [
                    { 
                        color: 'indigo', 
                        icon: 'pencil', 
                        small: true, // icon size
                        onClick: () => {} 
                    }
                ]
            */
        },

        mounted () {
            if (this.autoActive) {
                setTimeout(() => {
                    this.active = true
                }, 1000)
            }
        }
    }

</script>